@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.loading-container {
  width: 100%;
}
.loading-container .ant-spin-spinning {
  width: 100%;
}
.card-wrapper {
  width: 100%;
  min-height: 200px;
  background-color: white;
  display: grid;
  grid-template-columns: repeat(auto-fill, 210px);
  justify-content: space-between;
}
.card-wrapper .card {
  width: 210px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 15px;
}
.card-wrapper .card .card-content {
  height: 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 13px;
  color: #4E5159;
  text-align: left;
  font-weight: 500;
  background-color: #EBEDF1;
  padding-left: 10px;
}
.card-wrapper .card .card-content .create {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  color: #8C9099;
  font-weight: 400;
}
.card-wrapper .ant-card-head {
  height: 10px;
  min-height: 20px;
}
.card-wrapper .ant-card-extra {
  padding: 0;
}
.card-wrapper .ant-card-body {
  padding: 0;
}
.card-cover {
  width: 210px;
  height: 90px;
  background: #FFFFFF;
  border: 2px solid #ebedf1;
}
.card-cover .card-delete {
  width: 100%;
  height: 10px;
  display: flex;
  justify-content: flex-end;
}
.card-cover .card-delete .delete-icon {
  width: 10px;
  height: 10px;
  margin: 2px;
  background-image: url(data:image/gif;base64,);
  background-size: 100%;
}
.card-cover .card-player-top {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 8px;
  color: #000000;
  font-weight: 400;
  text-align: center;
}
.card-cover .card-player-top .name-left {
  position: absolute;
  left: 0px;
}
.card-cover .card-player-top .name-right {
  position: absolute;
  right: 0px;
}
.card-cover .card-player-top .vs {
  font-weight: 700;
}
.card-cover .card-player-bottom .match-score {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 58px;
}
.card-cover .card-player-bottom .match-score-vs {
  position: absolute;
  top: 0px;
  font-family: Arial-BoldMT;
  font-size: 8px;
  color: #000000;
  font-weight: 700;
}
.card-cover .card-player-bottom .match-score-detail {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 26px;
  font-family: Helvetica;
  font-size: 18px;
  color: #808080;
  font-weight: 400;
}
.card-cover .card-player-bottom .match-score-detail .score-highlight {
  color: #000000;
}
.card-cover .card-player-bottom .match-score-detail .score-divider {
  margin: 0 13px;
}
.card-cover .card-player-score {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.card-cover .card-player-score .left {
  position: absolute;
  left: 42px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.card-cover .card-player-score .right {
  position: absolute;
  right: 42px;
  -webkit-transform: translate(50%, 0);
          transform: translate(50%, 0);
}
.card-cover .card-player-score .card-player-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 70px;
  font-family: PingFangSC-Regular;
  font-size: 8px;
  color: #000000;
}
.card-cover .card-player-score .card-player-info-icon {
  margin-top: 4px;
}
.card-cover .card-player-score .card-player-info-icon .icon-img {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  box-shadow: 0 3px 5px 0 rgba(174, 190, 200, 0.5);
  object-fit: cover;
}
.card-cover .card-player-score .match-score {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70px;
}
.card-cover .card-player-score .match-score-vs {
  position: absolute;
  top: 6px;
  font-family: Arial-BoldMT;
  font-size: 8px;
  color: #000000;
  font-weight: 700;
}
.card-cover .card-player-score .match-score-detail {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 38px;
  font-family: Helvetica;
  font-size: 18px;
  color: #808080;
  font-weight: 400;
}
.card-cover .card-player-score .match-score-detail .score-highlight {
  color: #000000;
}
.card-cover .card-player-score .match-score-detail .score-divider {
  margin: 0 13px;
}
@media screen and (max-width: 524px) {
  .card-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .card-wrapper .card {
    width: 164px;
    margin-top: 15px;
  }
  .card-wrapper .card .card-content {
    height: 80px;
    font-size: 10px;
  }
  .card-cover {
    width: 164px;
    height: 76px;
    border: 1px solid #ebedf1;
  }
  .card-cover .card-delete {
    width: 100%;
    height: 10px;
  }
  .card-cover .card-delete .delete-icon {
    width: 7px;
    height: 7px;
    margin: 2px;
    background-image: url(data:image/gif;base64,);
    background-size: 100%;
  }
  .card-cover .card-player-score .left {
    position: absolute;
    left: 32px;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
  .card-cover .card-player-score .right {
    position: absolute;
    right: 32px;
    -webkit-transform: translate(50%, 0);
            transform: translate(50%, 0);
  }
  .card-cover .card-player-score .card-player-info {
    height: 64px;
    font-size: 7px;
  }
  .card-cover .card-player-score .card-player-info-icon {
    margin-top: 0%;
  }
  .card-cover .card-player-score .card-player-info-icon .icon-img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    box-shadow: 0 1px 2px 0 rgba(174, 190, 200, 0.5);
  }
  .card-cover .card-player-score .match-score {
    height: 64px;
  }
  .card-cover .card-player-score .match-score-vs {
    font-size: 7px;
  }
  .card-cover .card-player-score .match-score-detail {
    position: absolute;
    top: 30px;
    font-size: 18px;
  }
  .card-cover .card-player-score .match-score-detail .score-divider {
    margin: 0 10px;
  }
}
